html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #ffffff;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.boton__amarillo {
  background-color: #E08709;
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
}
.boton__amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton__amarillo--block {
  background-color: #E08709;
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
}
.boton__amarillo--block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton__verde {
  background-color: #71B100;
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
}
.boton__verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton__rojo--block {
  background-color: rgb(187, 7, 7);
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
}
.boton__rojo--block:hover {
  background-color: #a20606;
  cursor: pointer;
}

.boton__verde--block {
  background-color: #71B100;
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
}
.boton__verde--block:hover {
  background-color: #619800;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode .productos {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .btn__modo {
  filter: invert(100%);
}
.dark-mode .producto__descripcion {
  color: #ffffff;
}
.dark-mode .titulo_descripcion h4 {
  color: #ffffff;
}
.dark-mode .titulo_descripcion p {
  color: #e1e1e1;
}
.dark-mode .caracteristicas h4 {
  color: #ffffff;
}
.dark-mode .caracteristicas .cant {
  background-color: #1a1a1a;
  border-color: #ffffff;
  color: #ffffff;
}

.header {
  height: 55rem;
}
@media (min-width: 768px) {
  .header {
    height: 70rem;
  }
}
.header .img__header {
  width: 20rem;
  margin: 0 auto;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .header .img__header {
    margin-top: 15rem;
    width: 30rem;
  }
}

.barra {
  text-align: center;
  padding: 2rem 0 2rem 0;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.nav {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .nav {
    margin-top: 0;
  }
}

.nav a {
  display: block;
  text-align: center;
  font-size: 2rem;
  color: #9b5de5;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .nav a {
    display: inline;
    margin-left: 1rem;
  }
}

.nav a:hover {
  color: #E6CD75;
}

.logo__header h1 {
  font-weight: 600;
  color: #9b5de5;
}

.btn__modo {
  width: 3rem;
  height: 3rem;
  align-items: center;
  cursor: pointer;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .btn__modo {
    margin-top: 0;
  }
}

#color {
  color: #E6CD75;
}

.main {
  margin-top: 0rem;
}
@media (min-width: 768px) {
  .main {
    margin-top: 3rem;
  }
}

.main__titulo {
  padding-top: 2rem;
  text-transform: uppercase;
  font-weight: bold;
  color: #9b5de5;
  margin-bottom: 2.5rem;
}

@media (min-width: 768px) {
  .contenido__main {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.productos {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .productos {
    margin-bottom: 0;
  }
}

.productos__contenido {
  padding: 1rem;
}

.producto__descripcion {
  text-align: start;
  margin: 1.5rem 0 1.5rem 0;
}

.producto__precio {
  margin: 1.5rem 0 1.5rem 0;
  color: #9b5de5;
  font-size: 2.5rem;
  font-weight: bold;
}

.productos__titulo {
  color: #9b5de5;
}

.footer {
  margin-top: 5rem;
  background-color: #9b5de5;
}
.footer .logo__footer h1 {
  color: #ffffff;
}
.footer .nav a {
  color: #ffffff;
}
.footer .nav a:hover {
  color: #E6CD75;
}

.derechos__autor p {
  color: #ffffff;
  text-align: center;
  font-size: 2.2rem;
  padding-bottom: 2rem;
}

.main__nosotros {
  text-align: center;
}
@media (min-width: 768px) {
  .main__nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.main__nosotros--contenedor {
  text-align: center;
}
@media (min-width: 768px) {
  .main__nosotros--contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    text-align: center;
    grid-auto-flow: row;
  }
}

.logo__servicios {
  width: 3rem;
  height: 3rem;
}

.nuestros__servicios {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .nuestros__servicios {
    margin-top: 0;
  }
}

.main__nosotros--contenido h3 {
  color: #9b5de5;
}

.main__nosotros--servicios {
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .main__nosotros--servicios {
    margin-top: 0;
  }
}

.dark-mode .main__nosotros p {
  color: #ffffff;
}
.dark-mode .logo__servicios {
  color: #b689ec;
}

@media (min-width: 768px) {
  .contenedor__catalogo {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.contenido__blog {
  margin: 0 auto 2rem auto;
  background-color: #e1e1e1;
  border: 1px solid #c8c8c8;
}
@media (min-width: 768px) {
  .contenido__blog {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    border-bottom-right-radius: 2rem;
    max-width: 90%;
  }
}

.padding {
  padding: 1rem;
}
@media (min-width: 768px) {
  .padding {
    padding: 0;
  }
}

.dark-mode .contenido__blog {
  background-color: #333333;
  border: none;
}

.header__internas {
  height: 35rem;
}
@media (min-width: 768px) {
  .header__internas {
    height: 15rem;
  }
}

h4 {
  font-size: 1.8rem;
}

.contenedor_descripcion {
  padding: 1.5rem;
}
@media (min-width: 768px) {
  .contenedor_descripcion {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.main_titulo--desc {
  text-align: start;
}

.caracteristicas {
  padding-left: 1.5rem;
}

.color_rojo {
  width: 2.6rem;
  height: 2.6rem;
  background: red;
  border-radius: 50%;
}
.color_rojo:hover {
  width: 2.8rem;
  height: 2.8rem;
  transition: 0.5s;
}

.color_rosa {
  width: 2.6rem;
  height: 2.6rem;
  background: pink;
  border-radius: 50%;
}
.color_rosa:hover {
  width: 2.8rem;
  height: 2.8rem;
  transition: 0.5s;
}

.color_negro {
  width: 2.6rem;
  height: 2.6rem;
  background: #000000;
  border-radius: 50%;
}
.color_negro:hover {
  width: 2.8rem;
  height: 2.8rem;
  transition: 0.5s;
}

.color_azul {
  width: 2.6rem;
  height: 2.6rem;
  background: blue;
  border-radius: 50%;
}
.color_azul:hover {
  width: 2.8rem;
  height: 2.8rem;
  transition: 0.5s;
}

.colores {
  display: flex;
  gap: 1rem;
}

.caracteristicas_flex {
  display: flex;
  gap: 4rem;
  align-items: center;
}
.caracteristicas_flex .margin-0 {
  margin: 0;
  font-size: 1.7rem;
  text-align: center;
}

.cant {
  width: 5rem;
  height: 3rem;
  text-align: center;
  font-weight: bold;
  border: none;
  border-bottom: 1px solid #000000;
}

.cantidad, .colors {
  text-align: center;
}

.buttom {
  margin-top: 2rem;
  padding-left: 1.5rem;
}